              聊天室
            </div>
            <div class="tab-close">
              <i class="fas fa-times"></i>
              <!-- <img src="@/assets/images/close.svg" alt=""> -->
            </div>
          </div>
        </div>
        <div class="view">
          <div class="messages" :key="index" v-for="(msg, index) in channels">
            <!-- {{tt}}{{item.text}} -->
            <!-- <link rel="random" href="id22222已進入聊天室"> -->
            <div class="message-block" v-if="msg.uuid === 'system'">
              <span class="timestamp time">{{ msg.timestamp ? msg.timestamp.seconds : '' | timeFormat }}</span>
              <div class="system">
                <span v-if="msg.text !== nickname" :class="{'self-system': msg.text !== nickname}">&lt;!--</span>
