                <span class="tag" :class="{'self-system': msg.text !== nickname}">link&nbsp;</span>
                <span class="property" :class="{'self-system': msg.text !== nickname}">rel</span>
                <span class="text" :class="{'self-system': msg.text !== nickname}">=</span>
                <span class="value" :class="{'self-system': msg.text !== nickname}">"random"&nbsp;</span>
                <span class="property" :class="{'self-system': msg.text !== nickname}">type</span>
                <span class="text" :class="{'self-system': msg.text !== nickname}">=</span>
                <span class="value" :class="{'self-system': msg.text !== nickname}">"{{`system/log`}}"&nbsp;</span>
                <span class="property" :class="{'self-system': msg.text !== nickname}">href</span>
