                    <span class="comment">=</span>
                    <span class="comment">"{{msg.nickname}}"&nbsp;</span>
                    <span class="comment">class</span>
                    <span class="comment">=</span>
                    <span class="comment">"guest"</span>
                    <span class="comment">&gt;</span>
                  </div>
                  <!-- tag inside -->
