<script>
import { db } from '@/utils/db'
import firebase from 'firebase/app'
import moment from 'moment'
export default {
  name: 'HelloWorld',
  props: {
    msg: String,
    uuid: String
  },
  filters: {
    timeFormat(timestamp) {
      return timestamp ? moment.unix(timestamp).format('hh:mm') : ''
    }
  },
  data() {
    return {
      step: 1,
      mode: '',
      nickname: '',
      nicknameError: false,
      channel: null,
      channels: [],
      myMsg: ''
    }
  },
  mounted(){
    this.channel = db.collection('chatroom_01')
    // console.log(this.channel.orderBy('timestamp'))
  },
  firestore () {
    return {
      channels: db.collection('chatroom_01').orderBy('timestamp')
    }
  },
  computed:{
  },
  watch: {
    step(step) {
      if(step !== 4) {
        return
      }
      setTimeout(()=>{
        const el = document.querySelector('.view')
        // console.log(el, el.scrollTop, el.scrollHeight)
        // el.scrollTop = el.scrollHeight
        this.scrollTo(el, el.scrollHeight, 500)
      }, 500)
    },
    channels() {
      const el = document.querySelector('.view')
      // console.log(el, el.scrollTop, el.scrollHeight)
      // el.scrollTop = el.scrollHeight
      this.scrollTo(el, el.scrollHeight, 500)
    }
  },
  methods:{
    setNickname() {
      if(this.nickname === '') {
        this.nicknameError = true
        return
      }
      this.step = 3
    },
    scrollTo(element, to, duration) {
      if(duration <= 0) {
        return
      }
      const difference = to - element.scrollTop
      const perTick = difference / duration * 10

      setTimeout(() => {
        element.scrollTop = element.scrollTop + perTick
        if (element.scrollTop === to) {
          return
        }
        this.scrollTo(element, to, duration - 10)
      }, 10)
    },
    submit() {
      if(this.myMsg.replace(/\n/g, '') === '') {
        this.myMsg = `${this.myMsg}\n`
        return
      }
      const timestamp =  firebase.firestore.FieldValue.serverTimestamp()
      const message = this.myMsg.split('\n')

      this.channel.add({
        uuid: this.uuid,
        nickname: this.nickname,
        text: message,
        timestamp: timestamp
      })
      this.myMsg = ''
    },
    newline() {
      // console.log('newline')
      // this.myMsg = `${this.myMsg}`
    },
    enterChatroom() {
      this.step = 4
      const timestamp =  firebase.firestore.FieldValue.serverTimestamp()

      this.channel.add({
        uuid: 'system',
        text: `${this.nickname}`,
        timestamp: timestamp
      })
      this.message =  ''
    }
  }
}
</script>

